#wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 50rem;
    background-color: #131313c9;
    padding-top: 30px;
    padding-bottom: 10px;
    padding-left: 2rem;
    padding-right: 2rem;
    border-radius: 15px;
    margin-top: 2%;
}


#details {
    display: grid;
    margin-top: 20px;
    margin-bottom: 10px;
    font-weight: bold;
    font-size: 1.5rem;
} 

#pic {
    align-self: center;
    width: 85%;
    height: 80%;
    opacity: 0.8;
    margin-left: auto;
    margin-right: auto;
    border-radius: 2%;
}

#pic:hover {
    margin-left: 26%;
    margin-top: 4%;
    transform: scale(1.53);
    opacity: 100%;
    transition: 2.5s;
    z-index: 5;
    border-radius: 10%;
    box-shadow: rgba(22, 22, 22, 0.549) -4px 4px, rgba(59, 58, 60, 0.3) -10px 10px, rgba(236, 237, 250, 0.2) -15px 15px;
}


#project-title{
    color: #dcdcf1;
}

#stack {
    color: #dcdcf1;
    margin-left: -3px;
}

#info {
    color: #dcdcf1;
    font-size: 1 rem;
    margin-left: 2px;   
}

#infoitem {
    text-decoration: none;
    color: #5999E8;
    /* color: #dcdcf1; */
}

#infoitem:hover {
    color: #ed1682;
    color: #6fdfad
}

.link-text {
    color: #5999E8;
    font-size: 1 rem;
    margin-left: 2px; 
    text-decoration: none;
}

/* Media queries ipad */

@media only screen and (max-width: 820px) {
    #wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 40.2rem;
        background-color: #131313c9;
        padding-top: 30px;
        padding-bottom: 10px;
        padding-left: 2rem;
        padding-right: 2rem;
        border-radius: 15px;
        margin-top: 2%;
    }
    
    #details {
        display: grid;
        margin-top: 30px;
        margin-bottom: 10px;
        font-weight: bold;
        font-size: 1.5rem;
    } 
}





