#project-by-page-nav {
    display: grid;
    grid-template-rows: 
        'individual-title individual-back'
        'content-background';
}

#individual-title{
    grid-area: individual-title;
    color:rgb(249, 246, 246);
    font-size: 50px;
    justify-content: flex-start;
    box-shadow: #5999E8;
    margin-left: 8rem;
}

#individual-back {
    grid-area: individual-back;
    display: flex;
    margin-left: 45rem;
    margin-top: 2%;
    color:#393235;
    font-size: 2rem;
    text-decoration: none;
}

#individual-back:hover {
    color: blueviolet;
}

#content-background {
    display: flex;
    grid-area: content-background;
    background-color: rgba(0, 0, 0, 0.372);
    border-radius: 1rem;
    display: grid;
    grid-template-rows: 
        [individual-img individual-img] auto
        [individual-stack individual-stack] auto
        [individual-nature individual-nature] auto
        [individual-duration individual-duration] auto 
        [individual-info individual-info] auto
        [individual-challenge individual-challenge] auto
        [individual-solution individual-solution] auto
        [individual-feature individual-feature] auto
        [individual-link individual-demo-link] auto ; 
    width: 80%;
    margin-left: 6rem;
    margin-top: 7rem;
    padding: 2rem;
    font-size: 18px;
}

#individual-img{
    display: flex;
    grid-area: individual-img;
    height: 30rem;
    width: 60rem;
    justify-self: center;
    border: 4px solid #393235;
    border-radius: 1rem;
    box-shadow: 5px 4px #2c2c2d;
}

#individual-stack{
    margin-top: 2rem;
    display: flex;
    color: aliceblue;
    grid-area: individual-stack;
}

#individual-nature {
    margin-top: 2rem;
    display: flex;
    color: aliceblue;
    grid-area: individual-nature;
}

#individual-info{
    margin-top: 2rem;
    display: flex;
    color: aliceblue;
    grid-area: individual-info;
}

#individual-challenge{
    margin-top: 2rem;
    display: flex;
    color: aliceblue;
    grid-area: individual-challenge;
}

#individual-solution{
    margin-top: 2rem;
    display: flex;
    color: aliceblue;
    grid-area: individual-solution;
}

#individual-feature {
    margin-top: 2rem;
    display: flex;
    color: aliceblue;
    grid-area: individual-feature;
}

#individual-duration{
    margin-top: 2rem;
    display: flex;
    color: aliceblue;
    grid-area: individual-duration;
}

#individual-github-link{
    margin-top: 2rem;
    display: flex;
    grid-area: individual-link;
    font-family: 'Sigmar', cursive;
    /* color: rgb(224, 247, 246); */
    color: rgb(42, 34, 43);
    margin-left: 20rem;
    font-size: 1.7rem;
}

#individual-github-link:hover{
    /* color: rgb(42, 34, 43); */
    color: rgb(224, 247, 246);
}

#individual-demo-link{
    margin-top: 2rem;
    margin-left: 20rem;
    display: flex;
    grid-area: individual-demo-link;
    font-family: 'Sigmar', cursive;
    color: rgb(252, 201, 106);
    margin-left: 35rem ;
    font-size: 1.7rem;
}

#individual-demo-link:hover{
    color: rgb(106, 157, 252);
}


/* Media queries ipad */

@media only screen and (max-width: 820px) {
    #individual-title {
        grid-area: individual-title;
        color: rgb(249, 246, 246);
        font-size: 45px;
        justify-content: flex-start;
        box-shadow: #5999E8;
        margin-left: 2rem;
    }

    #individual-back {
        grid-area: individual-back;
        display: flex;
        text-align: end;
        margin-left: 26.5rem;
        margin-top: 2%;
        color: #393235;
        font-size: 25px;
        text-decoration: none;
    }

    #content-background {
        display: flex;
        grid-area: content-background;
        background-color: rgba(0, 0, 0, 0.372);
        border-radius: 1rem;
        display: grid;
        grid-template-rows:
            [individual-img individual-img] auto [individual-stack individual-stack] auto [individual-nature individual-nature] auto [individual-duration individual-duration] auto [individual-info individual-info] auto [individual-challenge individual-challenge] auto [individual-solution individual-solution] auto [individual-feature individual-feature] auto [individual-link individual-demo-link] auto;
        width: 84.5%;
        margin-left: 1.6rem;
        margin-top: 7rem;
        padding: 2rem;
        font-size: 18px;
    }

    #individual-img {
        display: flex;
        grid-area: individual-img;
        height: 25rem;
        width: 40rem;
        margin-left: -15px;
        border: 4px solid #393235;
        border-radius: 1rem;
        box-shadow: 5px 4px #2c2c2d;
    }

    #individual-github-link {
        margin-top: 2.5rem;
        display: flex;
        grid-area: individual-link;
        font-family: 'Sigmar', cursive;
        /* color: rgb(224, 247, 246); */
        color: rgb(42, 34, 43);
        margin-left: 8rem;
        font-size: 1.7rem;
    }

    #individual-demo-link {
        margin-top: 2.4rem;
        margin-left: 22rem;
        display: flex;
        grid-area: individual-demo-link;
        font-family: 'Sigmar', cursive;
        color: rgb(252, 201, 106);
        font-size: 1.7rem;
    }
}